.close-button {
  width: 15px;
  height: 15px;
  background: inherit;
  cursor: pointer;
  border: none;
  display: block;
  position: absolute;
  top: 20px;
  right: 20px;
}

.close-button:after, .close-button:before {
  content: "";
  width: 100%;
  height: 1px;
  transition: background-color var(--main-transition);
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 0;
}

.close-button:before {
  transform: rotate(45deg);
}

.close-button:after {
  transform: rotate(-45deg);
}

.close-button:hover:after, .close-button:hover:before, .close-button:focus:after, .close-button:focus:before {
  background-color: var(--pink-btn-hover);
}

